@font-face {
  font-family: 'ubuntu';
  src: url("/fonts/ubuntu-medium-webfont.woff2") format("woff2"), url("/fonts/ubuntu-medium-webfont.woff") format("woff");
  font-weight: medium;
  font-style: normal;
  font-display: optional;
}

@font-face {
  font-family: 'ubuntulight';
  src: url("/fonts/ubuntu-light-webfont.woff2") format("woff2"), url("/fonts/ubuntu-light-webfont.woff") format("woff");
  font-weight: lighter;
  font-style: normal;
  font-display: optional;
}

@font-face {
  font-family: 'ubuntubold';
  src: url("/fonts/ubuntu-b-webfont.woff2") format("woff2"), url("/fonts/ubuntu-b-webfont.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: optional;
}

@font-face {
  font-family: 'gagalinregular';
  src: url("/fonts/gagalin-regular-webfont/.woff2") format("woff2"), url("/fonts/gagalin-regular-webfont.woff") format("woff");
  font-style: normal;
  font-weight: normal;
  font-display: optional;
}

@font-face {
  font-family: 'lifehack';
  src: url("/fonts/lifehack_medium-webfont.woff2") format("woff2"), url("/fonts/lifehack_medium-webfont.woff") format("woff");
  font-weight: medium;
  font-style: normal;
  font-display: optional;
}

html {
  font-size: 16px;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-size: 16px;
  line-height: 1.42;
  background-color: black;
  position: relative;
}

.conteiner {
  max-width: 960px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.maincontent {
  height: 100%;
  transform: translateY(0);
  transition: 1s;
}

.section__wrapper {
  margin: 0 auto;
  position: relative;
  height: 100vh;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  margin: 0;
  padding: 0;
}

.btn {
  padding: 10px 15px;
  border-radius: 5px;
  display: inline-block;
}

.welcome {
  background-image: url(../../img/background/);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  overflow: hidden;
}

.header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding-top: 0.625rem;
  align-items: center;
}

.header__nav {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  margin-right: 0.6875rem;
  margin-top: -0.625rem;
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.nav__item {
  padding-left: 0.625rem;
}

.nav__item:last-child .nav__link:after {
  display: none;
}

.nav__link {
  display: flex;
  align-items: center;
  color: #fff;
  font-family: ubuntu;
  font-size: 0.8125rem;
  font-style: normal;
  font-weight: medium;
  text-transform: uppercase;
}

.nav__link:after {
  content: "";
  display: block;
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  background: #fff;
  align-items: center;
  margin-left: 0.625rem;
}

.nav__link:hover {
  text-decoration: underline;
}

.header__btn {
  background: #e35028;
  color: #fff;
  font-family: 'ubuntu';
  font-size: 0.8125rem;
  font-style: normal;
  font-weight: medium;
  text-transform: uppercase;
  margin-left: 0.9375rem;
}

.header__btn:hover {
  background: #c1401e;
}

.content {
  display: flex;
  width: 100%;
  align-items: center;
  margin-bottom: 5rem;
}

.content__burger {
  width: 50%;
  display: flex;
  justify-content: flex-end;
}

.content__desc {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.content__title {
  font-family: 'gagalinregular';
  color: #f9b43b;
  margin-top: 2.5rem;
  padding: 1.25rem 0.4375rem;
  text-align: start;
}

.content__text {
  font-size: 4.0625rem;
  font-family: 'lifehack';
  color: #fff;
  text-transform: lowercase;
  line-height: 1.01em;
  margin-left: 0.1875rem;
  letter-spacing: -0.3125rem;
}

.content__link {
  margin-bottom: 2.4375rem;
  display: flex;
  justify-content: center;
}

.content__arrow {
  width: 1.875rem;
}

.content__arrow:hover {
  width: 2.1875rem;
}

.hamburger-menu-link {
  vertical-align: middle;
  position: relative;
  height: 1.375rem;
  align-items: center;
  margin-left: 1.875rem;
  display: none;
}

.hamburger-menu-link__bars,
.hamburger-menu-link__bars:before,
.hamburger-menu-link__bars:after {
  background: #fff;
  width: 1.5rem;
  height: 0.3125rem;
  border-radius: 0.1875rem;
}

.hamburger-menu-link__bars:before,
.hamburger-menu-link__bars:after {
  content: "";
  display: block;
  position: absolute;
}

.hamburger-menu-link__bars:before {
  top: 0;
}

.hamburger-menu-link__bars:after {
  bottom: 0;
}

.mobail {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background-color: #2f3234;
  display: none;
}

.mobail__logo {
  position: fixed;
  top: 10px;
  left: 20px;
  cursor: pointer;
  z-index: 100;
}

.mobail__img {
  cursor: pointer;
}

.mobail__closing {
  width: 20px;
  height: 20px;
  position: fixed;
  top: 30px;
  right: 18px;
  cursor: pointer;
  z-index: 100;
}

.mobail__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  text-align: center;
  transition: all 3s;
}

.mobail__item {
  margin-bottom: 32px;
}

.mobail__item:last-child .mobail__link:after {
  display: none;
}

.mobail__link {
  align-items: center;
  color: #fff;
  font-family: 'gagalinregular';
  font-size: 1.5rem;
  font-style: normal;
  font-weight: normal;
  text-transform: uppercase;
}

.mobail__link:after {
  content: "";
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background: #fff;
  align-items: center;
  position: fixed;
  left: 50%;
  margin-top: 14px;
}

.mobail__link:hover {
  text-decoration: underline;
}

.webest {
  background-image: url(/background/best.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  overflow: hidden;
}

.webest__title {
