.composition {
  background-image: linear-gradient(90deg, #f9b43b 0%, #eb8c38 48%, #f9b43b 74%);
}

.composition__slaider {
  display: flex;
  position: relative;
}

.slaider__scrool {
  position: absolute;
  top: 50%;
  z-index: 2;
}

.slaider__scrool-left {
  left: 5%;
  writing-mode: vertical-rl;
  transform: rotate(90deg);
}

.slaider__scrool-reght {
  right: 5%;
  writing-mode: vertical-rl;
  transform: rotate(270deg);
}

.scrool {
  width: 1.0625rem;
  height: 1.875rem;
  fill: #ffffff;
}

.scrool:hover {
  fill: black;
}

.slaider {
  width: 100vw;
  height: 100vh;
  display: flex;
  overflow: hidden;
}

.slaider__list {
  display: flex;
  position: relative;
  will-change: right;
  transition: right 0.5s;
}

.slaider__conteiner {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  justify-content: center;
}

.slaider__compile {
