}

.slaider__compile .composit {
  width: 16.875rem;
  height: 20.625rem;
  background: #ffffff;
  border-radius: 2%;
  position: absolute;
  top: 0;
  left: 5.625rem;
  z-index: 100;
  display: none;
}

.slaider__compile .composit__list {
  margin: 1.125rem 1.25rem 1.3125rem 1.3125rem;
}

.slaider__compile .composit__item {
  display: flex;
