.menu {
  height: 100vh;
  background: url(/background/menu.jpg) no-repeat center center;
  background-size: cover;
  position: relative;
}

.menu__title {
  font-family: 'gagalinregular';
  color: #ffffff;
  position: absolute;
  top: 6.5625rem;
  left: 5.875rem;
}

.menu__list {
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 2;
}

.menu__item {
  width: 5rem;
  overflow: hidden;
  display: flex;
  transition: all 2s;
}

.menu__item--active {
  width: 38.75rem;
}

.menu__name {
  writing-mode: vertical-rl;
  transform: rotate(180deg);
