}

.menu__name:last-child {
  margin-left: -1.25rem;
}

.menu__name1 {
  background: url(../img/elements/menu1.jpg) no-repeat center center;
  background-size: cover;
}

.menu__name2 {
  background: url(../img/elements/menu2.jpg) no-repeat center center;
  background-size: cover;
}

.menu__name3 {
  background: url(../img/elements/menu3.jpg) no-repeat center center;
  background-size: cover;
}

.menu__text {
  min-width: 33.75rem;
  padding: 1.25rem;
  background-color: #2f3234;
  opacity: 0.929;
  color: #ffffff;
}

.comment__list {
  position: relative;
  width: 100%;
  display: flex;
  height: 100vh;
  min-height: 650px;
  flex-wrap: wrap;
  z-index: 0;
}

.comment__item {
  width: 25%;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
  transition: 2s;
}

.comment__item:hover .comment__info {
  opacity: 1;
}

.comment__item:hover {
  opacity: 0.9;
}

.nohover {
