  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
  transition: 2s;
}

.comment__info {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  padding: 0.625rem;
