  line-height: 2.008;
  text-align: left;
  letter-spacing: -0.09em;
  margin-bottom: 0.75rem;
}

.comment__text p {
  font-size: 0.875rem;
  font-family: 'ubuntulight';
  color: white;
  line-height: 1.714;
  margin-bottom: 0.75rem;
}

.comment__btn {
  font-size: 0.8125rem;
  font-family: "Ubuntu";
