.form__inform {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.form__row {
  display: flex;
  margin-bottom: 0.9375rem;
}

.form__block {
  flex: 1;
  margin-right: 1.25rem;
}

.form__block:last-child {
  margin-right: 0.625rem;
}

.form__block:first-child {
  margin-left: 0.625rem;
}

.form__input {
  width: 100%;
  height: 2.1875rem;
  padding: 5px;
  border-radius: 0.3125rem;
  border: none;
}

.form__input:hover {
  border: 0.1875rem solid #f9b43b;
}

.form__payment {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.form__comment {
  padding: 5px;
  height: 6.875rem;
  width: 100%;
}

.order__form-row {
  display: flex;
  margin-top: 1rem;
}

.order__radio {
  margin-left: 0.3125rem;
  display: flex;
}

.order__radio-fake {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background: #fff;
  position: relative;
}

.order__radio-fake:after {
  content: "";
  display: block;
  position: absolute;
  width: 0rem;
  height: 0rem;
  border-radius: 50%;
  background: #e35028;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: width .3s, height .3s;
}

.order__radio-elem {
  position: absolute;
  left: 6249.9375rem;
}

.order__radio-elem:checked + .order__radio-fake:after {
  width: 0.5rem;
  height: 0.5rem;
}

.order__radio-title {
  font-size: 0.875rem;
  color: #fff;
  margin-left: 0.625rem;
}

.form__chexbox {
  position: relative;
  margin-bottom: 2.625rem;
}

.form__check {
  width: 0.9375rem;
  height: 0.9375rem;
  background-color: #ffffff;
  position: absolute;
  left: 0.5rem;
  top: 0.5rem;
}

.check__img {
  position: absolute;
  top: 0.75rem;
  left: 0.625rem;
  z-index: 2;
  display: none;
}

.check__text {
  font-family: 'ubuntulight';
  font-size: 0.875rem;
  color: #ffffff;
  position: absolute;
  left: 2.0625rem;
  top: 0.375rem;
}

.form__checked--item {
  position: absolute;
  top: 70%;
  left: 999999999%;
}

.form__checked--item:checked + .check__img {
  display: block;
}

.form__btn {
  background-color: #e45028;
  font-family: 'ubuntulight';
  font-size: 0.875rem;
  color: #ffffff;
  border: none;
  text-transform: uppercase;
  cursor: pointer;
}

.form__btn:hover {
  background: #c1401e;
}

.form__btn--reset {
  background-color: rgba(255, 255, 255, 0);
  border: none;
  text-transform: capitalize;
  font-family: 'ubuntulight';
  font-size: 0.875rem;
  color: #ffffff;
  cursor: pointer;
}

.eror {
  color: red;
}

.pageScroll {
  position: fixed;
  top: 15%;
  right: 12%;
}

.pageScroll__item {
  position: relative;
  display: block;
  width: 0.3125rem;
  height: 0.3125rem;
  border-radius: 50%;
  background: #ffffff;
  padding: 0.1875rem;
  margin: 1.25rem 0.3125rem;
  z-index: 10000;
  cursor: pointer;
}

.activ::before {
  content: "";
  display: block;
  position: absolute;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  left: -5px;
  top: -5px;
  border: 1px solid #ffffff;
}

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background: rgba(49, 51, 52, 0.92);
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.modal__conteiner {
  width: 12.25rem;
  height: 6.75rem;
  border-radius: 5%;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  transition: all 3s;
}

.modal__title {
  font-family: 'ubuntulight';
  font-size: 0.875rem;
  line-height: 1.714;
  text-align: center;
  color: #2f3234;
  margin-bottom: 0.625rem;
}

.modal__btn {
  width: 6.125rem;
  background: #e45028;
  border-radius: 5%;
  color: #fff;
  font-family: 'ubuntu';
  font-size: 0.8125rem;
  font-style: normal;
  font-weight: medium;
  text-transform: uppercase;
}

.modal__btn:hover {
  background: #e34028;
}

.map {
  height: 100vh;
}

.map__img {
  width: 100px;
  height: 100px;
}

.maps {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.footer {
  background-color: #141414;
  position: absolute;
  display: flex;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
}

.footer__conteiner {
  display: flex;
  width: 960px;
}

.footer__contact {
  width: 50%;
  display: flex;
  flex-direction: column;
  margin: 0.625rem 0 0.625rem 0;
  padding-left: 0.625rem;
}

.footer__adress {
  font-family: 'ubuntulight';
  font-weight: lighter;
  font-size: 0.875rem;
  color: #ffffff;
}

.footer__phone {
  display: flex;
}

.icon__phone {
  fill: #e45028;
  width: 0.625rem;
  height: 1.25rem;
}

.footer__number {
  font-family: 'ubuntubold';
  font-weight: bold;
  font-size: 0.875rem;
  margin-left: 0.3125rem;
  color: #e45028;
}

.footer__icon {
  width: 50%;
}

.footer__list {
  display: flex;
  justify-content: flex-end;
  padding-top: 1.25rem;
  padding-right: 0.625rem;
}

.footer__item {
  margin-left: 1.25rem;
}

.icon__social {
  fill: #ffffff;
  width: 1.375rem;
  height: 1.375rem;
  fill-opacity: 0.5;
}

.icon__social:hover {
  fill-opacity: 1;
}

@media (max-width: 768px) {
  html {
    font-size: 14px;
  }

  .nav__list {
    display: none;
  }

  .content {
    flex-direction: column;
  }

  .content__desc {
    width: 100%;
  }

  .content__title {
    text-align: center;
    align-self: center;
  }

  .content__text {
    text-align: center;
    align-self: center;
  }

  .hamburger-menu-link {
    display: flex;
  }

  .webest__list {
    flex-wrap: wrap;
  }

  .webest__item {
    flex-wrap: wrap;
  }

  .webest__item {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
  }

  .slaider__conteiner {
    flex-direction: column;
    flex-wrap: nowrap;
  }

  .slaider__compile {
    top: 5%;
    left: 10%;
  }

  .slaider__kkal {
    top: 5%;
    left: 85%;
  }

  .slaider__images {
    width: 100%;
    justify-content: center;
    align-items: flex-start;
    margin-top: 4.375rem;
    margin-bottom: 1.25rem;
    overflow: inherit;
  }

  .burger__black {
    width: 20.625rem;
  }

  .slaider__content {
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-bottom: 3.125rem;
  }

  .slaider__title {
    text-align: center;
    margin-top: 1.25rem;
  }

  .slaider__text p {
    text-align: center;
  }

  .team__descript {
    width: 100%;
  }

  .team__title {
    margin-top: 6.25rem;
    text-align: center;
  }

  .accordeon__list {
    align-items: center;
    margin-top: 3.125rem;
  }

  .accordeon__item {
    align-items: center;
    margin-left: 0;
  }

  .accordeon__item--activ {
    height: 17.1875rem;
    transition: all 2s;
  }

  .accordeon__item--activ {
    height: 17.8125rem;
    transition: all 2s;
  }

  .accordeon__desc {
    flex-direction: column;
  }

  .accordeon__photo {
    margin: 0 auto;
  }

  .accordeon__prof {
    align-self: center;
  }

  .accordeon__text p {
    text-align: center;
    margin-right: 0;
  }

  .comment__item {
    width: 50%;
  }

  .nohover {
    width: 50%;
  }

  .comment__info {
    align-items: center;
  }

  .comment__name {
    margin: 0 auto;
    margin-bottom: 0.75rem;
  }

  .comment__text p {
    line-height: 1.0625rem;
  }

  .form__content {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 13px;
  }

  .meat {
    width: 4.375rem;
    height: 4.375rem;
  }

  .hand {
    width: 4.375rem;
    height: 4.375rem;
  }

  .natural {
    width: 4.375rem;
    height: 4.375rem;
  }

  .webest__section {
    width: 15rem;
    margin-bottom: 0.625rem;
  }

  .webest__subtitle {
    text-align: start;
    margin: 0 0 1.125rem 0;
  }

  .webest__text p {
    text-align: start;
    margin: 0;
  }

  .slaider__compile {
    top: 3%;
    left: 5%;
  }

  .burger__black {
    width: 18.75rem;
  }

  .menu__title {
    z-index: 3;
  }

  .comment__info {
    text-align: center;
    justify-content: center;
  }

  .comment__name {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .comment__text p {
    display: none;
  }

  .modalcom__conteiner {
    width: 15rem;
    height: 21.25rem;
  }

  .form__inform {
    width: 100%;
  }

  .form__block-floor {
    display: none;
  }

  .form__payment {
    width: 100%;
    justify-content: center;
  }

  .order__form-row {
    justify-content: center;
  }

  .form__chexbox {
    margin-bottom: 2.625rem;
    margin-left: 7.5rem;
  }

  .form__button {
    margin: 0 auto;
  }

  .footer__conteiner {
    flex-direction: column;
    align-items: center;
  }

  .footer__contact {
    align-items: center;
  }

  .footer__adress {
    justify-content: center;
    text-align: center;
  }

  .footer__phone {
    justify-content: center;
  }

  .footer__list {
    align-items: center;
    justify-content: center;
  }
}

@media (max-width: 468px) {
  .slaider__kkal {
    top: 3%;
    left: 80%;
  }
}